import * as Yup from 'yup';
import PropTypes from 'prop-types';
import { useMemo, useEffect } from 'react';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
// swal
import swal from 'sweetalert';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Alert, Stack } from '@mui/material';
import axios from '../../utils/axios';
import { FormProvider, RHFTextField } from '../../components/hook-form';

// drop dwon style
import useIsMountedRef from '../../hooks/useIsMountedRef';
// ----------------------------------------------------------------------
Habukhanint.propTypes = {
  discount: PropTypes.object,
};
export default function Habukhanint({ discount }) {
  const isMountedRef = useIsMountedRef();
  const NewUserSchema = Yup.object().shape({
  });
  const defaultValues = useMemo(
    () => ({
      Habukhan1_username: discount?.Habukhan1_username,
      Habukhan1_password: discount?.Habukhan1_password,
      Habukhan2_username: discount?.Habukhan2_username,
      Habukhan2_password: discount?.Habukhan2_password,
      Habukhan3_username: discount?.Habukhan3_username,
      Habukhan3_password: discount?.Habukhan3_password,
      Habukhan4_username: discount?.Habukhan4_username,
      Habukhan4_password: discount?.Habukhan4_password,
      Habukhan5_username: discount?.Habukhan5_username,
      Habukhan5_password: discount?.Habukhan5_password
    }),
    [discount]
  );
  const AccessToken = window.localStorage.getItem('accessToken');
  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
  });
  useEffect(() => {
    if (discount !== undefined) {
      reset(defaultValues);
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [discount]);
  const {
    reset,
    setError,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = methods;

  const onSubmit = async (data) => {
    try {
      await axios.post(`/api/edit/habukhanapi/charges/account/${AccessToken}/habukhan/secure`, data)
      swal('success', `Updated`, 'success');
    } catch (error) {
      if (isMountedRef.current) {
        setError('afterSubmit', error);
      }
    }
  };

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>

        <Grid item xs={12} md={8}>
          {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
          <Card sx={{ p: 3 }}>

            <Box
              sx={{
                display: 'grid',
                columnGap: 3,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(2, 1fr)' },
              }}
            >
              <RHFTextField name="Habukhan1_username" label="Habukhan 1 USERNAME" autoComplete="username" InputLabelProps={{ shrink: true }} />
              <RHFTextField name="Habukhan1_password" type="password" autoComplete="new-password" label="Habukhan 1 PASSWORD" InputLabelProps={{ shrink: true }} />
              <RHFTextField name="Habukhan2_username" label="Habukhan 2 USERNAME" autoComplete="username" InputLabelProps={{ shrink: true }} />
              <RHFTextField name="Habukhan2_password" type="password" autoComplete="new-password" label="Habukhan 2 PASSWORD" InputLabelProps={{ shrink: true }} />
              <RHFTextField name="Habukhan3_username" label="Habukhan 3 USERNAME" autoComplete="username" InputLabelProps={{ shrink: true }} />
              <RHFTextField name="Habukhan3_password" type="password" autoComplete="new-password" label="Habukhan 3 PASSWORD" InputLabelProps={{ shrink: true }} />
              <RHFTextField name="Habukhan4_username" label="Habukhan 4 USERNAME" autoComplete="username" InputLabelProps={{ shrink: true }} />
              <RHFTextField name="Habukhan4_password" type="password" autoComplete="new-password" label="Habukhan 4 PASSWORD" InputLabelProps={{ shrink: true }} />
              <RHFTextField name="Habukhan5_username" label="Habukhan 5 USERNAME" autoComplete="username" InputLabelProps={{ shrink: true }} />
              <RHFTextField name="Habukhan5_password" type="password" autoComplete="new-password" label="Habukhan 5 PASSWORD" InputLabelProps={{ shrink: true }} />

            </Box>
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
                Proceed
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
